import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import {TestApp} from './TestApp';

serviceWorker.unregister();

document.querySelectorAll('.reactions-root').forEach(root => {
    const resourceId = encodeURIComponent(root.getAttribute('data-resource-id'));
    ReactDOM.render(<App resourceId={resourceId}/>, root);
});

const testRoot = document.getElementById('test-root');
if (testRoot) {
    ReactDOM.render(<TestApp/>, testRoot);
}
