import React, {Component} from 'react';
import axios from 'axios';
import './App.css';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faFrown, faSmile} from '@fortawesome/free-regular-svg-icons';


let baseUrl;
if (process.env.NODE_ENV === 'development') {
    baseUrl = 'http://localhost:8080'
} else {
    baseUrl = 'https://reactions-backend.herokuapp.com'
}

class App extends Component {
    constructor() {
        super();

        this.state = {
            countLikes: '…',
            countDislikes: '…',
            reacted: false
        };

        this.addLike = this.addLike.bind(this);
        this.addDislike = this.addDislike.bind(this);
        this.loadData = this.loadData.bind(this);
    }

    componentDidMount() {
        return this.loadData();
    }

    addLike() {
        if (this.state.reacted) {
            return;
        }

        this.setState({reacted: true});

        return axios.post(`${baseUrl}/resources/${this.props.resourceId}/like`)
            .then(() => this.loadData());
    }

    addDislike() {
        if (this.state.reacted) {
            return;
        }

        this.setState({reacted: true});

        return axios.post(`${baseUrl}/resources/${this.props.resourceId}/dislike`)
            .then(() => this.loadData());
    }

    loadData() {
        return axios.get(`${baseUrl}/resources/${this.props.resourceId}`)
            .then(response => {
                let data = response.data;
                this.setState({
                    countLikes: data.likes,
                    countDislikes: data.dislikes
                });
            });
    }

    render() {
        return (
            <div className="reaction">
                <div className="reaction__likes" onClick={this.addLike}>
                    <FontAwesomeIcon className="reaction__likes-icon" icon={faSmile}/>
                    <p className="reaction__likes-count">{this.state.countLikes}</p>
                </div>
                <div className="reaction__dislikes" onClick={this.addDislike}>
                    <FontAwesomeIcon className="reaction__dislikes-icon" icon={faFrown}/>
                    <p className="reaction__dislikes-count">{this.state.countDislikes}</p>
                </div>
            </div>
        );
    }
}

export default App;